export default {
	name: 'UniIndexedList',
	props: {
		options: {
			type: Array,
			default () {
				return [];
			}
		},
		showSelect: {
			type: Boolean,
			default: false
		},
		otherHeight:{
			//其他高度
			type: Number
		}
	},
	data() {
		return {
			lists: [],
			touchmove: false,
			touchmoveIndex: -1,
			itemHeight: 0,
			winHeight: 0,
			scrollViewId: ''
		};
	},
	created() {
		let winHeight = uni.getSystemInfoSync().windowHeight - this.otherHeight;//1.减去外面的其他组件高度
		this.itemHeight = winHeight / this.options.length;
		this.winHeight = winHeight;
		this.lists = this.options.map(value => {
			let items = value.data.map(item => {
				let obj = {};
				obj['key'] = value.letter;
				obj['name'] = item
				obj.checked = item.checked ? item.checked : false;
				return obj;
			});
			return {
				title: value.letter,
				key: value.letter,
				items: items
			};
		});
	},
	methods: {
		touchStart(e) {
			this.touchmove = true;
			let pageY = e.touches[0].pageY;
			let index = Math.floor((e.target.offsetTop - this.otherHeight) / this.itemHeight);//2.减去外面的其他组件高度
			//let index = Math.floor(pageY / this.itemHeight);
			let item = this.lists[index];
			if (item) {
				this.scrollViewId = 'uni-indexed-list-' + item.key;
				this.touchmoveIndex = index;
			}
		},
		touchMove(e) {
			let pageY = e.touches[0].pageY;
			//let index = Math.floor(pageY / this.itemHeight);
			let index = Math.floor((e.target.offsetTop - this.otherHeight) / this.itemHeight);//2.减去外面的其他组件高度
			let item = this.lists[index];
			if (item) {
				this.scrollViewId = 'uni-indexed-list-' + item.key;
				this.touchmoveIndex = index;
			}
		},
		touchEnd() {
			this.touchmove = false;
			this.touchmoveIndex = -1;
		},
		onClick(idx, index) {
			let obj = {};
			for (let key in this.lists[idx].items[index]) {
				obj[key] = this.lists[idx].items[index][key];
			}
			let select = [];
			if (this.showSelect) {
				this.lists[idx].items[index].checked = !this.lists[idx].items[index].checked;
				this.lists.forEach((value, idx) => {
					value.items.forEach((item, index) => {
						if (item.checked) {
							let obj = {};
							for (let key in this.lists[idx].items[index]) {
								obj[key] = this.lists[idx].items[index][key];
							}
							select.push(obj);
						}
					});
				});
			}
			this.$emit('click', {
				item: obj,
				select: select
			});
		}
	}
};
